.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.black-nav {background: #222; padding:5px 15px; color:#fff; text-align: left;}

.list {text-align: left; border-bottom:1px solid #efefef;}
.list h4 {padding:0px 0px 0px 15px;}
.list p {padding:0px 0px 0px 15px; color:#696969; font-size:0.9em;}

.modal {background: #fafafa; padding:15px; text-align: left;}